@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700);
body{
  font-family: 'Montserrat', sans-serif;
  background: #ecf0f1;
  text-align: center;
}

h1{
  font-size: 64px;
  font-weight: 300;
  margin: 80px 0 25px;
  color: #2c3e50;
}

h1 span {
  font-weight: 700;
}

h2 {
  color: #bdc3c7;
  font-weight: 300;
}

p {
  color: #bdc3c7;
  font-weight: 300;
}

ul {
  list-style-type: none;
  width: 800px;
  margin: 0 auto;
}

li {
  width: 100%;
  height: 60px;
  line-height: 60px;
  font-size: 20px;
  padding: 0 20px;
  color: #34495e;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

li span:first-child {
  float: left;
}

li:hover {
  background: rgba(0, 0, 0, 0.02);
  cursor: pointer;
}

li span:nth-child(2) {
  opacity: 0;
  float: right;
  color: #c0392b;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

li:hover span:nth-child(2) {
  opacity: 1;
}

.form {
  width: 800px;
  margin: 30px auto ;
}

#todoInput {
  width: 100%;
  height: 60px;
  background: none;
  border: none;
  border-bottom: 1px solid #34495e;
  font-size: 1.2em;
}

*:focus {
  outline: none;
}



